.container {
  padding-inline: 20px;
}

@media (width >= 920px) {
  .container {
    padding-inline: 34px;
  }
}

@media (width >= 1200px) {
  .container {
    padding-inline: 55px;
  }
}

html, body {
  height: 100%;
  margin: 0;
}

body {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.card {
  text-align: center;
  background: #f5f5f5;
  border-radius: 10px;
  flex: 0 0 300px;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

.page {
  background: url("bg.2f7aa6aa.png") 0 0 / contain no-repeat;
  flex-grow: 1;
  height: 100%;
}

.page__container {
  max-width: 1200px;
  margin: 0 auto;
}

.page__slider {
  max-width: 940px;
  margin: 50px auto;
  position: relative;
  overflow: hidden;
}

.slider__btn {
  color: #fff;
  cursor: pointer;
  z-index: 10;
  background: #222;
  border: none;
  padding: 10px;
  font-size: 24px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slider__btn.prev {
  left: -50px;
}

.slider__btn.next {
  right: -50px;
}

.slider__track {
  gap: 20px;
  transition: transform .4s;
  display: flex;
}
