@import 'utils/variables';
@import 'utils/mixin';
@import 'blocks/card';

$card-width: 300px;
$gap: 20px;

html, body {
	height: 100%;
	margin: 0;
}

body {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}


.card {
	flex: 0 0 $card-width;
	background: #f5f5f5;
	border-radius: 10px;
	padding: 20px;
	text-align: center;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.page {
	height: 100%;
	flex-grow: 1;
	background: url('../assets/bg.png') 0 0 no-repeat;
	background-size: contain;

		&__container {
			max-width: $desktop-width;
			margin: 0 auto;
		}

		&__slider {
			position: relative;
			max-width: calc(#{$card-width} * 3 + #{$gap} * 2);
			margin: 50px auto;
			overflow: hidden;
		}

		&__title{
			
		}
}
.slider {

		&__btn {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			background: #222;
			color: #fff;
			font-size: 24px;
			padding: 10px;
			border: none;
			cursor: pointer;
			z-index: 10;

			&.prev {
				left: -50px;
			}

			&.next {
				right: -50px;
			}
		}

		&__track {
			display: flex;
			gap: $gap;
			transition: transform 0.4s ease;
		}
}
